:root {
    --highlight: #02617f52;
    --highlight_two: #ffffff36;
    --font-color: #ffffffbd;
    --content_background: #070112;
    --dark_background: #070708;
    --root_background: black;
    --svg_color: #ffffffa6;
    --light_background: white;
    --light_text: black;
}

.background {
    background: var(--root_background);
    color: var(--font-color);
}

.preparing_page {
    color: var(--font-color);
    background: var(--content_background);
}

html {
    background: var(--dark_background)
}

.selector_container {
    position: absolute;
    top: 7rem;
    left: 2rem;
    width: calc(100vw - 4rem);
    background: var(--dark_background);
    background: #161e26;
    color: var(--font-color);
    padding: 1rem;
    border: 1px solid var(--highlight);
    border-radius: 1rem;
    z-index: 100;
}

.header_anchor:hover {
    color: white;
}

.Heatmap {
    background-color: transparent;
    color: transparent;
    font-size: 0.8rem;
    cursor: default;
    border: 1px solid #02617f52;
}

.Hourslot {
    background-color: transparent;
    color: transparent;
}

.LowHeat {
    background: rgb(150, 150, 150);
}

.Hot {
    background: rgb(100, 100, 100);
}

.SuperHot {
    background: rgb(50, 50, 50);
}

.WhiteHot {
    background: rgb(25, 25, 25);
}

.Top {
    color: var(--font-color);
    text-align: center;
    font-weight: bold;
    background: #161e26a1;
}

.graph_selector {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(30% - 2rem);
    background: var(--content_background);
    border: 1px solid var(--highlight_two);
    border-radius: 1rem;
    padding: 1rem;
    margin: 0.5rem 0rem;
}

.graph_button {
    background: transparent;
    background: #161e26a1;
    border: none;
    color: var(--svg_color);
    border: 1px solid var(--highlight_two);
    border-radius: 1rem;
    padding: 0.5rem;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hide_before_hover {
    display: none;
    margin: 0;
    font-weight: bold;
    color: var(--font-color);
}

.graph_button:hover {
    background: #e9ecef;
    background: #ffffff60;
    -webkit-box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
    -moz-box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
    box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
    cursor: pointer;
}

.light > .graph_button:hover {
    background:rgb(150, 150, 150);
    color:rgb(25, 25, 25);
}

.table_header {
    font-size: 1.5rem;
    width: 100%;
    display: flex;
    justify-content: center;
}

.quantity {
    font-size: 0.8rem;
    margin-left: 0.1rem;
    color: #ffffff87;
}

.ui_box {
    background: var(--content_background);
    border: 1px solid var(--highlight_two);
    border-radius: 1rem;
    padding: 1rem;
    margin: 0.5rem 0rem;
    display: flex;
    justify-content: center;
    color: var(--font-color);
    cursor: default;
}

.main-container {
    width: 76%;
    min-height: 100vh;
    margin: 0 auto;
    background: var(--dark_background);
    border-left: 1px solid var(--bs-gray-dark);
    border-right: 1px solid var(--bs-gray-dark);
}

.page_body {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.5rem 2rem;
    margin: 0;
    border-top: 1px solid var(--bs-gray-dark);
    /* border-bottom: 1px solid var(--bs-gray-dark); */
    /* background: var(--dark_background); */
    flex-direction: column;
}

.data-table>tbody>tr>td>a {
    border: none;
    padding: 0.5rem 0;
    font-size: 0.8rem;
    text-decoration: none;
    color: var(--font-color);
}

.data-table>tbody>tr>td>a:hover {
    color: white;
}

.input_addornments {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: transparent;
    color: black;
    height: fit-content;
    border: 1px solid var(--bs-gray-dark);
    border-radius: 1.5rem;
    padding: 0.1rem;
}

.header_anchor {
    color: var(--font-color);
    text-decoration: none;
    font-size: 1rem;
    margin-right: 2rem;
}

.input_addornments:hover {
    background: #e9ecefd3;
    -webkit-box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
    -moz-box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
    box-shadow: 0px 0px 22px 0px rgba(255, 255, 255, 0.31);
}

.header_button {
    border: none;
    background: transparent;
    color: var(--svg_color);
}

.type_break {
    border-bottom: 1px solid var(--bs-gray-dark);
}

.input_addornments>svg {
    padding-right: 1rem;
    color: var(--svg_color);
}

.input_addornments>input {
    background: transparent;
    border: none;
    margin-left: 1rem;
}

.top_container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.5rem 2rem;
    margin: 0;
    border-top: 1px solid var(--bs-gray-dark);
}

.input_addornments:hover>svg {
    color: var(--bs-gray-dark);
}

.table_settings {
    margin-top: 1rem;
}

.data-table {
    width: 100%;
}

.hidden {
    display: none;
}

.heatmap_legend {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-weight: bold;
    opacity: 0.5;
    font-size: 0.8rem;
}

.graph_button:hover .hide_before_hover {
    display: block;
}

.graph_button:hover .icon_svg {
    display: none;
}

#top_container_name {
    display: flex;
    flex-direction: column;
    flex-direction: row;
    align-items: center;
    margin-right: 1rem;
}

.product_name {
    margin-top: 5px;
    font-size: 1.2rem;
    font-weight: 800;
}

.name_and_price>p {
    margin-bottom: 0rem;
    font-size: 1.2rem;
    font-weight: 800;
}

.the_data {
    width: 70%;
    height: 376px;
    max-height: 376px;
    align-items: center;
}

.data-table>thead>tr>th {
    font-size: 1rem;
    width: 25%;
}

.user_interface {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 0.5rem 2rem;
}

textarea:focus,
input:focus {
    outline: none;
}

.header_search::-moz-selection {
    border: none;
}

.navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 2rem;
    border-bottom: 1px solid var(--bs-gray-dark);
}

.preparing_page {
    display: flex;
    font-size: 1.5rem;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.top_container_name>p {
    margin-bottom: 0;
}

.navAnchors {
    font-weight: 800;
}

.market_info {
    display: none;
}

.market_info_body_row_column>p {
    margin-bottom: 0.5rem;
    font-size: 0.8rem;
}

.chatGPT {
    width: 50%;
}

.chatGPT>p {
    font-size: 0.8rem;
    margin-bottom: 0;
}

#data_mode {
    width: 100%;
    flex-direction: row;
    display: flex;
    justify-content: space-between;
}

.bottom_half_container {
    display: flex;
    justify-content: space-between;
}

.data_mode_buttons {
    width: calc(50% - 1rem);
}

.display_option {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.setting_column > input {
    margin-right: 1rem;
}

.setting_header {
    font-size: 1rem;
}

.setting_description {
    font-size: 0.8rem;
}

.radio_div {
    display: flex;
    flex-direction: column;
}

.light {
    background: var(--light_background);
    color: var(--light_text);
}

.light:hover {
    /* background: var(--light_background); */
    color: var(--light_text);
}

.false {
    display: none;
}

.true {
    display: block;
}

.light > * {
    color: var(--light_text);
}

.light > div > table > tbody >tr>td>a {
    color: var(--light_text);
}

.light > div > table > tbody >tr>td>a:hover {
    color: var(--light_text);
}

.dark > div > table > tbody >tr>td>a {
    color: var(--dark_text);
}

.light > svg {
    color: black;
}

.light .ui_box {
    border: 1px solid var(--bs-gray-dark);
    background: #161e26a1;
}

.light > .graph_button {
    border: 1px solid var(--bs-gray-dark);
}

.toggle_graphs_view {
    display: flex;
    flex-direction: row;
    padding: 0.5rem 2rem;
    border-top: 1px solid var(--bs-gray-dark);
}

.toggle_graphs_view > label {
    margin-right: 1rem;
}

/* mobile styles */
@media all and (max-width: 995px) {
    .header_search {
        width: 100%;
    }

    .main-container {
        width: 100%;
    }

    .page_body {
        flex-direction: column;
    }

    .ui_box {
        width: 100%;
    }

    .top_container {
        padding: 0.5rem;
    }

    .the_data {
        width: 100%;
    }

    .graph_selector {
        flex-direction: row;
        width: 100%;
    }

    .page_body {
        padding: 0.5rem 0.5rem;
    }

    .graph_button {
        padding: 1rem;
        width: calc((100% / 3) - 1rem);
        line-height: 20px;
    }

    .user_interface {
        padding: 0.5rem;
    }

    /* .Heatmap {
        font-size: 0.5rem;
    } */

    .input_addornments {
        margin-top: 1.5rem;
        margin-bottom: 1rem;
        width: 80%;
        padding: 0.3rem;
    }

    .navbar {
        flex-direction: column;
        padding: 0.5rem;
    }

    .navAnchors {
        display: flex;
        width: 90%;
        justify-content: space-between;
    }

    .header_anchor {
        margin: 0;
    }

    .bottom_half_container {
        flex-direction: column;
    }
}